import React, { Component } from 'react';
import './App.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';
import 'moment/locale/en-gb';

const apiURL = 'https://marcuskivi.com/films/films';

class App extends Component {
  constructor(props) {
        super(props);
        this.state = {
            searchfilms: [],
            ownFilms: [],
            inputName: '',
            date: moment(),
            loading: true,
            timeout: 0,
            user_id: document.getElementById("userid") ? document.getElementById("userid").innerText : 1,
        };
    }
    componentDidMount() {
        this.getFilms();
    }
    getFilms() {
        this.setState({
            loading: true
        });
        let url = apiURL;
        url+='?user_id='+this.state.user_id;
        console.log("url is " + url);
        fetch(url)
		.then((response) => response.json())
		.then((responseData) => {
            console.log(responseData);
            const formattedFilms = responseData.result.map(film => {
                return {
                    id: film.id,
                    date_seen: moment(film.date_seen).format('DD.MM.YYYY'),
                    title: film.title
                }
            })
            this.setState({
				ownFilms: formattedFilms,
                loading: false
			});
		});
    }
    inputChanged = (event) => {
        this.setState({[event.target.name]: event.target.value});
    }
    dateChanged = (event) => {
        this.setState({date: event});
    }
    handleSubmit = (event) => {
        event.preventDefault();
        if (this.state.inputName.length > 0) {
            const film = {date_seen: moment(this.state.date).format('YYYY-MM-DD'),
                         title: this.state.inputName,
                         user_id: this.state.user_id};
            fetch(apiURL, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
              body: JSON.stringify(film)
           }).then((response) => response.json())
		      .then((responseData) => {
				  this.getFilms();
			});
        }
    }
    handleDelete  = (event) => {
          fetch(apiURL + "/" + event.target.name, {
			  method: 'DELETE'
		    }).then((response) => response.json())
		      .then((responseData) => {
				  this.getFilms();
			});
    }
    render() {
        const itemRows = this.state.ownFilms.map((film) => 
          <tr key={film.id}>
            <td className="col-2">{film.date_seen}</td>
            <td>{film.title}</td>
            <td className="col-1"><button name={film.id} className="btn btn-danger" onClick={this.handleDelete}>Delete</button></td>
          </tr>
         );
        let table;
        if (!this.state.loading && this.state.ownFilms.length > 0) {
            table = (
            <div>
              <table className="table table-striped mt-3">
                <thead className="thead-light"><tr><th>Date</th><th>Title</th><th></th></tr></thead>
                <tbody>
                  {itemRows}
                </tbody>
            </table>
          </div>
            );
        }
      return (
          <div>
            <h1>Filmlist</h1>
            <div className="form-group row">
                <div className="col-sm-2 col-xs-12">
                  <DatePicker id="datepicker" selected={this.state.date} onChange={this.dateChanged} dateFormat="DD.MM.YYYY" locale="en-gb" />
                  </div>
                <div className="col-sm-6 col-xs-12">
                    <input id="inputName" className="form-control" type="text" placeholder="Film name" name="inputName" onChange={this.inputChanged} value={this.state.inputName} />
                </div>
            <div className="col-sm-2 col-xs-12">
            <button className="btn btn-primary" onClick={this.handleSubmit}>Save</button>
            </div>
          </div>
          {table}
          </div>
    );
    }
}

export default App;



